.overlap-content {
  width: 40%;
  background: #96c3ce;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(135deg, #0f2027, #203a43, #2c5364, #219ca5) !important;
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364, #219ca5) !important;
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: 150% 150%;
  background-position: center;
  position: absolute;
  top: -150px;
  left: 50%;
  transform: translateX(-50%);
  height: 300px;
  border: 2px solid #96c3ce;
  border-radius: 15px;
  -webkit-box-shadow: 0 8px 6px -6px #AFBED3;
  -moz-box-shadow: 0 8px 6px -6px #AFBED3;
  box-shadow: 0 8px 6px -6px #AFBED3;
  color: white;
  overflow: hidden;
  padding: 1rem; }
  .overlap-content h1 {
    font-family: 'Cairo', sans-serif; }
  .overlap-content .globe {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    height: 250%;
    width: 250%;
    opacity: .25; }

