/* Contact SVG */
.sizehandler {
  width: 100%;
  height: 100%; }

.svg-container {
  display: flex;
  align-items: center; }
  .svg-container .s-text {
    animation: spaper 1.3s ease-in-out infinite alternate;
    transform-origin: center;
    transform-box: fill-box; }
  .svg-container .brief {
    animation: brief 1.8s ease-in-out infinite alternate;
    transform-origin: top;
    transform-box: fill-box; }
  .svg-container .shadow-back {
    animation: brief 1.8s ease-in-out infinite alternate;
    transform-origin: top;
    transform-box: fill-box; }
  .svg-container .sinthaesia-s {
    animation: sinthaesias 2s ease infinite alternate;
    transform-origin: bottom;
    transform-box: fill-box; }
  .svg-container .gradient-transition {
    animation: gradients 1s ease infinite;
    transform-origin: center;
    transform-box: fill-box; }

.filltext {
  fill: #203a43; }

@keyframes gradients {
  from {
    fill: green; }
  to {
    fill: beige; } }

@keyframes spaper {
  from {
    transform: rotateX(0deg);
    transform: scale(1); }
  to {
    transform: rotateX(22deg);
    transform: scale(1.05); } }

@keyframes brief {
  from {
    transform: rotateX(0deg);
    transform: translateY(0); }
  to {
    transform: rotateX(22deg);
    transform: translateY(2rem); } }

@keyframes sinthaesias {
  from {
    transform: translateY(0); }
  to {
    transform: translateY(1.1rem); } }

